  export default {
    name: "Alert",
    components: { FlexLayout },

    data() {
      return {
        alertList: [
          {
            id: 1,
            title: "Fuel Theft",
          },
          {
            id: 2,
            title: "Speeding",
          },
          {
            id: 3,
            title: "Device Expired",
          },
          {
            id: 4,
            title: "Power Cut",
          },
          {
            id: 5,
            title: "Ignitation Test",
          },
        ],
        scheduleList: [
          {
            id: 1,
            time: "12:00 am",
            day: "Sunday",
          },
          {
            id: 2,
            time: "12:00 am",
            day: "Monday",
          },
          {
            id: 3,
            time: "12:00 am",
            day: "Thursday",
          },
        ],
      };
    },
  };
</script>
